.header {
  background-color: var(--primary);
}

.header  .innerContent {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 40px;
}

.header a {
  color: var(--white);
  font-size: 1.25rem;
  text-decoration: none;
  transition: all 0.3s ease;
}

.header a:hover {
  opacity: 0.7;
}

.header .brand a {
  font-weight: 700;
  font-size: 1.5rem;
}

.header .navLinks {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  align-items: center;
}

.header .navLinks li {
  display: inline-block;
 
  margin-right: 30px;
}


.header .navLinks li:last-child {
  margin-right: 0;
}



.btn {
padding: 12px 20px;
background-color: var(--secondary);
color: var(--primary);
border-radius: 5px;
text-transform: uppercase;
font-weight: 700;
display: inline-block;
border: none;
font-size: 1rem;
transition: all 0.3s ease;
line-height: 1.1;
}

.btn:hover {
background-color: #33fe9c;
opacity: 1;
cursor: pointer;
}

.btn:disabled {
pointer-events: none;
opacity: 0.5;
}

input:focus {
outline: none;
}


@media (max-width: 576px){
  .header .navLinks li {
    display: inline-block;
   font-size: .7em;
    margin-right: 1em;
  }
  
  .header  .innerContent {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 4px;
  }


  .header .navLinks {
    margin-left: 1em;

  }

  .header .brand a {
    font-weight: 700;
    font-size: 1.4em;
  }
  
  
.btn {
  padding: 5px 5px;

  font-weight: 700;
  display: inline-block;
  border: none;
  font-size: .6rem;
  font-weight: 400;
 
  }
}